import React from "react";
import PropTypes from "prop-types";
import { graphql, Link } from "gatsby";

import { Layout, PostCard, Pagination } from "../components/common";
import { MetaData } from "../components/common/meta";

import "../components/common/Layout/post.scss";

/**
 * Main index page (home page)
 *
 * Loads all posts from Ghost and uses pagination to navigate through them.
 * The number of posts that should appear per page can be setup
 * in /utils/siteConfig.js under `postsPerPage`.
 *
 */
const Index = ({ data, location, pageContext }) => {
    const posts = data.allGhostPost.edges;

    posts.forEach((post) => {
        post.node.featured = false;
    });

    posts[0].node.featured = true;

    const techPosts = data.techPosts.edges;
    techPosts.forEach((post) => {
        post.node.featured = false;
    });

    techPosts[0].node.featured = true;

    const dataPosts = data.dataPosts.edges;
    dataPosts.forEach((post) => {
        post.node.featured = false;
    });

    dataPosts[0].node.featured = true;

    const designPosts = data.designPosts.edges;
    designPosts.forEach((post) => {
        post.node.featured = false;
    });

    designPosts[0].node.featured = true;

    const storiesPosts = data.storiesPosts.edges;
    storiesPosts.forEach((post) => {
        post.node.featured = false;
    });

    storiesPosts[0].node.featured = true;

    const culturePosts = data.culturePosts.edges;
    culturePosts.forEach((post) => {
        post.node.featured = false;
    });

    culturePosts[0].node.featured = true;

    const newsPosts = data.newsPosts.edges;
    newsPosts.forEach((post) => {
        post.node.featured = false;
    });

    newsPosts[0].node.featured = true;

    const tags = data.tags.edges;
    return (
        <div className="first-section">
            <MetaData location={location} />
            <Layout location="/blog/">
                <div className="p-0">
                    <div className="bg-dark kernel-banner go-jek-work-gradient relative-position" />
                </div>

                <div className="container py-5">
                    {tags.map(({ node }) => (
                        <Link
                            key={node.slug}
                            to={`tag/${node.slug}`}
                            className="text-white mr-2"
                        >
                            <span className="category badge rounded-pill py-2 px-3 mt-2 mt-md-0">
                                {node.name}
                            </span>
                        </Link>
                    ))}
                    <Link
                        key="all"
                        to="articles/all"
                        className="text-white mr-2 mb-3 mb-md-0"
                    >
                        <span className="category badge rounded-pill py-2 px-3 mt-2 mt-md-0">
                            All
                        </span>
                    </Link>
                </div>

                <div className="container posts">
                    <div className="mb-5">
                        <section className="post-feed pb-3">
                            <h2 className="maison-bold mb-5">Latest</h2>
                            <div className="row">
                                {posts.map(({ node }) => (
                                    <PostCard key={node.id} post={node} />
                                ))}
                            </div>
                        </section>
                        <Link
                            to="/articles/all/"
                            className="text-green view-all font-sm"
                        >
                            View All{` `}
                            <i
                                className="fa fa-long-arrow-right"
                                aria-hidden="true"
                            ></i>
                        </Link>

                        {/* Culture Started Posts */}
                        <section className="post-feed pb-3">
                            <h2 className="maison-bold mb-5">Culture</h2>
                            <div className="row">
                                {culturePosts.map(({ node }) => (
                                    <PostCard key={node.id} post={node} />
                                ))}
                            </div>
                        </section>
                        <Link
                            to="/tag/culture/"
                            className="text-green view-all font-sm"
                        >
                            View All{` `}
                            <i
                                className="fa fa-long-arrow-right"
                                aria-hidden="true"
                            ></i>
                        </Link>
                        {/* End Culture posts */}

                        {/* Data Started Posts */}
                        <section className="post-feed pb-3">
                            <h2 className="maison-bold mb-5">Data</h2>
                            <div className="row">
                                {dataPosts.map(({ node }) => (
                                    <PostCard key={node.id} post={node} />
                                ))}
                            </div>
                        </section>
                        <Link
                            to="/tag/data/"
                            className="text-green view-all font-sm"
                        >
                            View All{` `}
                            <i
                                className="fa fa-long-arrow-right"
                                aria-hidden="true"
                            ></i>
                        </Link>
                        {/* End Data posts */}

                        {/* Design Started Posts */}
                        <section className="post-feed pb-3">
                            <h2 className="maison-bold mb-5">Design</h2>
                            <div className="row">
                                {designPosts.map(({ node }) => (
                                    <PostCard key={node.id} post={node} />
                                ))}
                            </div>
                        </section>
                        <Link
                            to="/tag/design/"
                            className="text-green view-all font-sm"
                        >
                            View All{` `}
                            <i
                                className="fa fa-long-arrow-right"
                                aria-hidden="true"
                            ></i>
                        </Link>
                        {/* End Design posts */}

                        {/* News Started Posts */}
                        <section className="post-feed pb-3">
                            <h2 className="maison-bold mb-5">News</h2>
                            <div className="row">
                                {newsPosts.map(({ node }) => (
                                    <PostCard key={node.id} post={node} />
                                ))}
                            </div>
                        </section>
                        <Link
                            to="/tag/news/"
                            className="text-green view-all font-sm"
                        >
                            View All{` `}
                            <i
                                className="fa fa-long-arrow-right"
                                aria-hidden="true"
                            ></i>
                        </Link>
                        {/* End News posts */}

                        {/* Tech Started Posts */}
                        <section className="post-feed pb-3">
                            <h2 className="maison-bold mb-5">Tech</h2>
                            <div className="row">
                                {techPosts.map(({ node }) => (
                                    <PostCard key={node.id} post={node} />
                                ))}
                            </div>
                        </section>
                        <Link
                            to="/tag/tech/"
                            className="text-green view-all font-sm"
                        >
                            View All{` `}
                            <i
                                className="fa fa-long-arrow-right"
                                aria-hidden="true"
                            ></i>
                        </Link>
                        {/* End Tech posts */}

                        {/* Stories Started Posts */}
                        <section className="post-feed pb-3">
                            <h2 className="maison-bold mb-5">Stories</h2>
                            <div className="row">
                                {storiesPosts.map(({ node }) => (
                                    <PostCard key={node.id} post={node} />
                                ))}
                            </div>
                        </section>
                        <Link
                            to="/tag/stories/"
                            className="text-green view-all font-sm"
                        >
                            View All{` `}
                            <i
                                className="fa fa-long-arrow-right"
                                aria-hidden="true"
                            ></i>
                        </Link>
                        {/* End Stories posts */}
                    </div>
                </div>
            </Layout>
        </div>
    );
};

Index.propTypes = {
    data: PropTypes.shape({
        allGhostPost: PropTypes.object.isRequired,
    }).isRequired,
    location: PropTypes.shape({
        pathname: PropTypes.string.isRequired,
    }).isRequired,
    pageContext: PropTypes.object,
};

export default Index;

// This page query loads all posts sorted descending by published date
// The `limit` and `skip` values are used for pagination
export const pageQuery = graphql`
    query GhostPostQuery($skip: Int!) {
        allGhostPost: allGhostPost(
            limit: 4
            filter: { featured: { eq: false } }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        techPosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "tech" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        dataPosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "data" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        designPosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "design" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        storiesPosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "stories" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        culturePosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "culture" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        newsPosts: allGhostPost(
            filter: {
                featured: { eq: false }
                tags: { elemMatch: { slug: { eq: "news" } } }
            }
            sort: { order: DESC, fields: [published_at] }
            skip: $skip
            limit: 4
        ) {
            edges {
                node {
                    ...GhostPostFields
                }
            }
        }

        tags: allGhostTag {
            edges {
                node {
                    name
                    slug
                }
            }
        }
    }
`;
