import React from "react";
import PropTypes from "prop-types";
import { Link } from "gatsby";
import { Tags } from "@tryghost/helpers-gatsby";
import Moment from "react-moment";
import { readingTime as readingTimeHelper } from "@tryghost/helpers";

const PostCard = ({ post }) => {
    const url = `/blog/${post.slug}/`;
    const readingTime = readingTimeHelper(post);

    return (
        <>
            {post.featured && (
                <>
                    <div className="col-md-8 position-relative post-margin featured">
                        <Link to={url} className="post-card">
                            {post.feature_image && (
                                <div
                                    className="post-card-image featured"
                                    style={{
                                        backgroundImage: `url(${post.feature_image})`,
                                    }}
                                ></div>
                            )}
                        </Link>
                    </div>
                    <div className="col-md-4 post-margin">
                        <Link to={url} className="post-card">
                            <h2 className="maison-bold font-lg-x title">
                                {post.title}
                            </h2>
                            <p className="excerpt">
                                {`${post.excerpt.substring(0, 100)}...`}
                            </p>
                            <footer className="footer font-xs position-absolute roboto-regular">
                                <div className="author text-green font-weight-bold">
                                    {post.primary_author.name}
                                </div>
                                <div className="date-time">
                                    <span>{readingTime}</span> &nbsp;| &nbsp;
                                    <span>
                                        <Moment format="MMM DD, YYYY">
                                            {post.published_at}
                                        </Moment>
                                    </span>
                                </div>
                            </footer>
                        </Link>
                    </div>
                </>
            )}

            {!post.featured && (
                <>
                    <div className="col-md-6 col-lg-4 position-relative post-margin">
                        <Link to={url} className="post-card">
                            {post.feature_image && (
                                <div
                                    className="post-card-image"
                                    style={{
                                        backgroundImage: `url(${post.feature_image})`,
                                    }}
                                ></div>
                            )}
                            <h2 className="maison-bold font-lg title pt-3">
                                {post.title}
                            </h2>
                            <p className="excerpt">
                                {`${post.excerpt.substring(0, 100)}...`}
                            </p>
                            <footer className="post-footer font-xs position-absolute">
                                <div className="author text-green font-weight-bold">
                                    {post.primary_author.name}
                                </div>
                                <div className="date-time">
                                    <span>{readingTime}</span> &nbsp;| &nbsp;
                                    <span>
                                        <Moment format="MMM DD, YYYY">
                                            {post.published_at}
                                        </Moment>
                                    </span>
                                </div>
                            </footer>
                        </Link>
                    </div>
                </>
            )}
        </>
    );
};

PostCard.propTypes = {
    post: PropTypes.shape({
        slug: PropTypes.string.isRequired,
        title: PropTypes.string.isRequired,
        feature_image: PropTypes.string,
        featured: PropTypes.bool,
        tags: PropTypes.arrayOf(
            PropTypes.shape({
                name: PropTypes.string,
            })
        ),
        excerpt: PropTypes.string.isRequired,
        primary_author: PropTypes.shape({
            name: PropTypes.string.isRequired,
            profile_image: PropTypes.string,
        }).isRequired,
    }).isRequired,
};

export default PostCard;
