import React from "react";
import PropTypes from "prop-types";
import { Link } from "gatsby";

const Pagination = ({ pageContext }) => {
    const {
        previousPagePath,
        nextPagePath,
        humanPageNumber,
        numberOfPages,
    } = pageContext;

    return (
        <nav
            className="pagination mt-5 pt-5 justify-content-center font-weight-bold"
            role="navigation"
        >
            <div className="mr-3">
                {previousPagePath && (
                    <Link to={previousPagePath} rel="prev">
                        <i className="fa fa-chevron-left text-green"></i>
                    </Link>
                )}
            </div>
            {numberOfPages > 1 && (
                <div className="pagination-location pb-5">
                    Page{` `}
                    <span className="text-green font-weight-bold">
                        {humanPageNumber}
                    </span>
                    {` `}
                    of {numberOfPages}
                </div>
            )}
            <div className="ml-3">
                {nextPagePath && (
                    <Link to={nextPagePath} rel="next">
                        <i className="fa fa-chevron-right text-green"></i>
                    </Link>
                )}
            </div>
        </nav>
    );
};

Pagination.propTypes = {
    pageContext: PropTypes.object.isRequired,
};

export default Pagination;
